<template lang="">
  <div>
    <TransitionRoot appear :show="isOpen" as="template">
      <Dialog as="div" @close="closeModal" class="relative z-10">
        <TransitionChild
          as="template"
          enter="duration-300 ease-out"
          enter-from="opacity-0"
          enter-to="opacity-100"
          leave="duration-200 ease-in"
          leave-from="opacity-100"
          leave-to="opacity-0"
        >
          <div class="fixed inset-0 bg-black bg-opacity-25" />
        </TransitionChild>

        <div class="fixed inset-0 overflow-y-auto">
          <div
            class="flex h-full items-center justify-center p-4 text-center"
          >
            <TransitionChild
              as="template"
              enter="duration-300 ease-out"
              enter-from="opacity-0 scale-95"
              enter-to="opacity-100 scale-100"
              leave="duration-200 ease-in"
              leave-from="opacity-100 scale-100"
              leave-to="opacity-0 scale-95"
            >
              <DialogPanel
                class=" lg:w-3/5 w-5/6 h-2/3 transform overflow-hidden rounded-2xl bg-white p-6 text-left align-middle shadow-xl transition-all"
              >
                <DialogTitle
                  as="h3"
                  :key="project_name"
                  class=" sm:text-2xl text-xl leading-6 text-gray-900 font-acme"
                >
                  {{project_name}}
                </DialogTitle>

                <div class=" flex mt-6 flex-col lg:flex-row" > 
                  <p class=" lg:w-1/2 w-full content-p" :key="project_content">
                    {{project_content}}
                  </p>
                  <img class="lg:w-1/2 w-2/3 h-1/2" :src="img_src">
                  
                </div>

                <div class=" flex mt-5 flex-row  justify-self-start">

                  <div >
                    <button
                      type="button"
                      class="btn focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2"
                      @click="closeModal"
                    >
                      Close
                    </button>
                  </div>

                  <div class=" bottom-3 ml-5">
                    <a v-show="addLink" :href="link" class="btn focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2">Video link</a>
                  </div>

                </div>

              </DialogPanel>
            </TransitionChild>
          </div>
        </div>
      </Dialog>
    </TransitionRoot>
  </div>
</template>

<script>
import { TransitionRoot,  TransitionChild,  Dialog,  DialogPanel,  DialogTitle } from '@headlessui/vue';
import templateImg from '../img/spring_fox.jpg';
import greygooseImg from '../img/gallery/gggg.jpg';
import reportImg from '../img/gallery/report1.png';
import ledImg from '../img/gallery/led.jpg';
import tcImg from '../img/gallery/tc.png';
import axisImg from '../img/gallery/AXIS-1.png';
import pointcloud from '../img/gallery/pointcloud.png';

export default {
  name: 'PopWin',
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogPanel,
    DialogTitle,
  },
  props:{
    val: {
      type: Number,
      default: 3,
      required: true
    },

    isOpen: {
        type: Boolean,
        default: false,
        required: true
    },

    flexColumn:{
      type: Boolean,
      default: false,
      required: false
    }
  },

  data() {
    return {
      project_name_list: ['Greygoose', 'Touch Cloud Website', 'Abaci Report', 'Axis Camera Plugin and AI Analysis System', 'NEXCOM Mechanical Arm Computer Vision AI Analysis System', 'Internship : 3D Scanning with Optical Inspection Instruments'],
      project_name:  'Abaci Report',
      project_content:'',
      img_src: templateImg,
      openModal: false,
      addLink: false,
      link:''
    }
  },

  mounted(){
    this.project_content=" A local website for statistical report:  The main challenges were to deal with the gorgeous amount of data from AI engine, and present in a nice way, according charts , gauge capacity. "
    this.img_src=reportImg
  },

  watch:{

    val:{
      handler(val, oldVal) {
        this.project_name = this.project_name_list[val-1]
        console.log(this.project_name)

        if(val == 1){
          this.project_content="My main task is to build the whole AI engine, which is responsible of detection and representation. I built the entire framework, including inference engine,  id tracker,  fence detection, and more. "
          this.addLink = true
          this. link = "https://youtu.be/_11o5ehJsTc"
          this.img_src=greygooseImg
        }
        else if(val==2){
          this.project_content=" In this project, as a Front-end developer, I was responsible of the entire design of the website, and introduce product to client clearly. "
          this.img_src=tcImg
          this.addLink = true
          this. link = "https://www.touchcloud.com.tw/"
        }
        else if(val==3){
          this.project_content=" A local website for statistical report:  The main challenges were to deal with the gorgeous amount of data from AI engine, and present in a nice way, according charts , gauge capacity. "
          this.img_src=reportImg
          console.log(this.project_content)
          this.addLink = false
        }
        else if(val==4){
          this.project_content=" Detect motion and send camera frame to AI server. "
          this.img_src=axisImg
          this.addLink = true
          this. link = "https://youtu.be/CXtt25TVekc"

        }
        else if(val==5){
          this.project_content=" A system for detection LED lamp :    Detect position , LED lamp rotation angle and color."
          this.img_src=ledImg
          this.addLink = false
        }
        else if(val==6){
          this.project_content=" Point Cloud Visualization System:    User could operate system with point cloud data , analyze and rebuild point cloud, then display with detail information."
          this.img_src=pointcloud
          this.addLink = false
        }

      }
    },

    isOpen:{
        handler(val, oldVal){
            this.openModal = val;
            console.log("isOpen:", val);
        }
    }
  },
  methods:{
    closeModal(){
      this.openModal = false;
      this.$emit('isClose', this.openModal);
    },
  }
}
</script>

<style>

  .btn{
    @apply  inline-flex justify-center rounded-md border border-transparent bg-blue-100 px-4 py-2 text-sm font-medium text-blue-900 hover:bg-blue-200 ;
  }

  .content-p{
    @apply font-catamaran text-lg text-gray-500 m-2;
  }
</style>