<style lang="scss">
@import 'node_modules/sassdash';
@import 'node_modules/sass-svg/index';

  $duration: 17s;
  $size: 0.07;
  $color-fox: #e37c67;
  
/* cat animation */

.fox {
	position: absolute;
	height: 375vmax * $size;
	width: 260vmax * $size;
	animation: fox $duration none, squigglevision 0.3s infinite;
	animation-timing-function: cubic-bezier(0, 0, 0.80, 0.90);
	bottom: 2.5vmax;
	left: 60%;
	transform-origin: bottom right;

	*, *:before, *:after {
		animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1) !important;
	}

	* {  
		position: absolute;
		background-size: contain;
		background-repeat: no-repeat;
		transform-style: preserve-3d;
		
		&:before, &:after {
			content: '';
			position: absolute;
			display: block;
			background-size: contain;
			background-repeat: no-repeat;
			height: 100%;
			width: 100%;
		}
	}
}

.lyingCat {
  
  .text{
    @apply mx-9 ;

    .header-1{
      @apply font-acme 2xl:text-8xl xl:text-7xl;
      text-align: left;
    }

  }
  
  .content--cat {
    background: url('../img/cherryTree.png') no-repeat 100% 0%; //center center fixed
    background-size: 45%;
    position: absolute;
    pointer-events: none;
    top: 0;
    //left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    left:50%;
    transform: translateX(-50%); 

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-position: bottom center;
        background-repeat: no-repeat;
        background-size: cover;
