</template>

<script>
// @ is an alias to /src
import Flickity from 'vue-flickity';
import ProjectName from './project_title.vue';
import PopWin from './PopWin.vue';

export default {
  name: 'FlickityImg',
  components: {
    Flickity,
    ProjectName,
    PopWin
  },
  data() {
    return{
      flickityOptions: {
        initialIndex: 3,
        draggable:true,
        prevNextButtons: false,
        pageDots: false,
        wrapAround: false,
        freeScroll: true,
        // any options from Flickity can be used
        
      },
      selectedIndex: 3,
      isOpen: false,
      val: 3,
      flexCol: true
    }
  },
  watch:{
    winHeight: (newVal, oldVal)=>{
      console.log(newVal)
    }

  },
  methods: {
    onInit() {
      this.$refs.flickity.on('change', (event) => {
        this.selectedIndex = event
        console.log('current index', event)
      })
    },

    openModal(val){
      this.isOpen = true
      this.val = val
    },
    change(close){
      this.isOpen = close
    },

  }
}
</script>

<style lang="scss">

.FlickityImg{

  .flickWrap{
    margin-top: 20vh;

    .carousel-cell {
      width: 200px;
      margin-right: 80px;
      background: #8C8;
      //border-radius: 5px;
      counter-increment: gallery-cell;
      
      @apply flex justify-center overflow-hidden text-center;

      img {
        max-height: 100%;
        margin: 0 0;
        max-width: none;
        width: auto;

      }

      /* en_border */
      .en_border.left{
        @apply absolute bottom-0 top-0;
        left: -3px;
      }

      .en_border.right{
        @apply absolute bottom-0 top-0;
        right: -3px;
      }

      .en_border.top{
        @apply absolute ;
        left: 1px;
        right: 5px;
        top: -2px;
        will-change: width;

        svg{
          height: 8px;
        }
      }

      .en_border.bottom{
        @apply absolute;
        left: 1px;
        right: 5px;
        bottom: -2px;
        will-change: width;

        svg{
          height: 8px;
        }

      }

    }

  }
    
}

</style>
